  background: url(../img/arrow-right.png) no-repeat 50% 50%;
  right: 19.5%;
}

@media only screen and (max-width: 960px) {
  .slick-next {
    right: 40px;
  }
}

[dir="rtl"] .slick-next {
  left: 19.5%;
  right: auto;
}

.slick-dots {
  position: absolute;
  bottom: 22px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  width: 100%;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.slick-dots li button {
  display: block;
  padding: 3px;
  background: none;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: red;
  cursor: pointer;
}

.slick-dots li button:before {
  content: "";
  display: block;
  height: 10px;
  width: 10px;
  box-sizing: border-box;
  border-radius: 50%;
  background: #979795;
  transition: all 0.1s ease;
}

.slick-dots li button:hover:before {
  background: #4E4E4E;
}

.slick-dots li.slick-active button:before {
  background: #4E4E4E;
}

.slick-initialized .slick-slide, .slick-slide img {
  display: flex;
  align-items: center;
}

