.hamburger {
  display: none;
  margin-left: auto;
  width: 40px;
  height: 40px;
  margin-top: 10px;
}

@media only screen and (max-width: 960px) {
  .hamburger {
    display: block;
  }
}

.hamburger-icon {
  float: left;
  width: 30px;
  height: 23px;
  overflow: hidden;
  transition: 0.3s;
}

.hamburger-icon span {
  float: left;
  width: 30px;
  height: 3px;
  background-color: #fffefe;
  position: relative;
  top: 10px;
  transition: 0.3s;
}

.hamburger-icon span:before {
  content: '';
  width: 30px;
  height: 3px;
  background-color: #fffefe;
  position: absolute;
  top: -8px;
  left: 0;
}

.hamburger-icon span:after {
  content: '';
  width: 30px;
  height: 3px;
  background-color: #fffefe;
  position: absolute;
  bottom: -8px;
  left: 0;
}

.hamburger-icon.active {
  transform: rotate(360deg);
}

.hamburger-icon.active span {
  background-color: transparent;
}

.hamburger-icon.active span:before {
  top: 0;
  transform: rotate(-45deg);
}

.hamburger-icon.active span:after {
  bottom: 0;
  transform: rotate(45deg);
}

